import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { Field, Form } from "react-final-form";
import CircularProgress from "@mui/material/CircularProgress";
import { FileField, InputField, Error } from "common/components";
import { PageMode, required } from "common";
import { FacilityService, CountryService, CityService, AttachmentService } from "admin/services";

function AddUpdateForm(props) {
  const [t] = useTranslation();
  const { mode, selectedFacility, onClose } = props;
  const [isLoading, setIsLoading] = useState(false);
  const [isCityLoading, setIsCityLoading] = useState(false);
  // const [isCountryLoading, setIsCountryLoading] = useState(false);
  const [message, setMessage] = useState();
  const [countriesData, setCountriesData] = useState([]);
  const [citiesData, setCitiesData] = useState([]);
  const [selectedCountry, setSelectedCountry] = useState();
  const [imageLoading, setImageLoading] = useState(false);
  const [image, setImage] = useState();
  const [uploadImage, setUploadImage] = useState(true);

  const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

  useEffect(async () => {
    if (mode === PageMode.Update && selectedFacility?.attachment?.id) {
      setImageLoading(true);
    }
    setSelectedCountry(selectedFacility?.countryId);
    setIsCityLoading(true);
    const countryResponse = await CountryService.getAllCountries();
    const cityResponse = await CityService.getAllCities();
    setMessage(!!countryResponse?.message && countryResponse?.message);
    if (countryResponse.success) {
      setCountriesData(countryResponse.body);
    }
    if (cityResponse.success) {
      setCitiesData(cityResponse.body);
    }
    setIsCityLoading(false);
    if (mode === PageMode.Update && selectedFacility?.attachment?.id) {
      const responseAttachment = await AttachmentService.getAttachmentById(
        selectedFacility?.attachment?.id
      );
      if (responseAttachment.success) {
        setImage(responseAttachment.body);
        setUploadImage(false);
      } else setImage(null);
      setImageLoading(false);
    }
  }, []);

  const onSubmit = async (values) => {
    console.log(`onSubmit :: ${JSON.stringify(values, 0, 2)}`);
    setIsLoading(true);
    const facilityData = {
      name: values.name,
      city: values.city,
      details: values.details,
      active: mode === PageMode.Update ? values.active : true,
    };
    let requestData = new FormData();
    requestData.append("request", JSON.stringify(facilityData, 0, 2));
    if (uploadImage) requestData.append("image", values.image[0]);

    let response;
    if (mode === PageMode.Update) {
      response = await FacilityService.updateFacility(
        selectedFacility?.id,
        requestData
      );
    } else {
      response = await FacilityService.createFacility(requestData);
    }

    if (!!!response.success && !response.success) {
      setIsLoading(false);
      setMessage(!!response.message && response.message);
      return;
    }
    setIsLoading(false);
    onClose(response.body);
  };

  const validate = async (values) => {
    const errors = {};
    if (!values.name) {
      errors.name = required;
    }
    if (!values.country) {
      errors.country = required;
    }
    if (!values.city) {
      errors.city = required;
    }
    if (!values.details) {
      errors.details = required;
    }
    if (!values.image && uploadImage) {
      errors.image = required;
    }
    return errors;
  };

  let form = (
    <Form
      mutators={{
        setValue: ([field, value], state, { changeValue }) => {
          changeValue(state, field, () => value);
        },
      }}
      validate={validate}
      onSubmit={onSubmit}
      initialValues={{
        name: selectedFacility?.name,
        country: selectedFacility?.countryId,
        city: selectedFacility?.cityId,
        details: selectedFacility?.details,
        image: selectedFacility?.image,
      }}
      render={({
        handleSubmit,
        onChange,
        submitError,
        form,
        submitting,
        pristine,
        values,
        form: {
          mutators: { setValue },
        },
      }) => (
        <form onSubmit={handleSubmit} className="needs-validation" noValidate>
          {message && (
            <div className="invalid-feedback d-block mb-2">{message}</div>
          )}
          <div className="row form-row">
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>{t("Facility.Country")}</label>
                <Field name="country" showerrorbeside="true">
                  {({ input, meta }) => {
                    return (
                      <select
                        className="form-control select"
                        {...input.select}
                        defaultValue={values.country}
                        value={values.country}
                        onChange={(e) => {
                          input.onChange(e);
                          setSelectedCountry(e.target.value);
                          setValue("city", "");
                        }}
                      >
                        <option value="">
                          {isCityLoading === true
                            ? t("loading")
                            : t("Facility.SelectCountry")}
                        </option>
                        {countriesData?.map((val, index) => (
                          <option key={index} value={val.id}>
                            {val.name}
                          </option>
                        ))}
                      </select>
                    );
                  }}
                </Field>
                <Error name={"country"} showerrorbeside="true" />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>{t("Facility.City")}</label>
                <Field name="city" showerrorbeside="true">
                  {({ input, meta }) => {
                    return (
                      <select
                        className="form-control select"
                        {...input.select}
                        defaultValue={values?.city}
                        value={values?.city}
                        disabled={
                          !values?.country ||
                          citiesData.findIndex(
                            (c) => c.country?.id == selectedCountry
                          ) < 0
                        }
                        onChange={(e) => {
                          input.onChange(e);
                        }}
                      >
                        <option value="">
                          {isCityLoading === true
                            ? t("loading")
                            : !!selectedCountry &&
                              citiesData.findIndex(
                                (c) => c.country?.id == selectedCountry
                              ) < 0
                            ? "No cities found"
                            : t("Facility.SelectCity")}
                        </option>
                        {citiesData
                          ?.filter((c) => c.country?.id == selectedCountry)
                          .map((val, index) => (
                            <option key={index} value={val.id}>
                              {val.name}
                            </option>
                          ))}
                      </select>
                    );
                  }}
                </Field>
                <Error name={"city"} showerrorbeside="true" />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                <label>{t("Facility.Name")}</label>
                <Field
                  name="name"
                  className="form-control floating"
                  type="text"
                  component={InputField}
                />
              </div>
            </div>
            <div className="col-12 col-md-6">
              <div className="form-group">
                {imageLoading ? (
                  <CircularProgress />
                ) : mode === PageMode.Update && image && !uploadImage ? (
                  <>
                    <img src={image?.fileUrl} alt={"image"} width={250} />
                    <span
                      title="remove"
                      onClick={() => setUploadImage(true)}
                      style={{ cursor: "pointer", marginTop: "44px" }}
                    >
                      ❌
                    </span>
                  </>
                ) : (
                  <>
                    <label>{t("Country.Image")}</label>
                    <FileField name="image" />
                    <Error name={"image"} showerrorbeside="true" />
                  </>
                )}
              </div>
            </div>
            <div className="col-12 col-md-12">
              <div className="form-group">
                <label>{t("Country.Details")}</label>
                <Field
                  name="details"
                  className="form-control"
                  type="textarea"
                  rows="4"
                  placeholder="Write here the details..."
                  component={InputField}
                />
              </div>
            </div>
          </div>
          <div className="submit-section">
            <button
              type="submit"
              className="btn btn-primary submit-btn"
              disabled={isLoading}
            >
              {!isLoading ? t(`Facility.Save`) : ""}
              {isLoading && (
                <span className="spinner-border spinner-border-sm mb-1"></span>
              )}
            </button>
            <button
              className="btn btn-secondary submit-btn"
              onClick={(e) => {
                e.preventDefault();
                onClose(null);
              }}
            >
              {t("Facility.Close")}
            </button>
          </div>
        </form>
      )}
    />
  );

  return <div>{form}</div>;
}
export default AddUpdateForm;
