import { useEffect, useState } from "react";
import { Modal } from "react-bootstrap";
import { useTranslation } from "react-i18next";
import StickyBox from "react-sticky-box";
import Alert from "@mui/material/Alert";
import CircularProgress from "@mui/material/CircularProgress";
import DashboardSidebar from "client/components/dashboard/sidebar/sidebar.jsx";
import { FacilityService } from "admin/services";
import { DataTable } from "common/components";
import { PageMode, TableInputType } from "common";
import AddUpdateForm from "./AddUpdateForm";

const FacilitiesList = () => {
  const [t] = useTranslation();
  const [loading, setLoading] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [show, setShow] = useState(false);
  const [showDelete, setShowDelete] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);
  const [message, setMessage] = useState();
  const [messageDelete, setMessageDelete] = useState();
  const [facilitiesData, setFacilitiesData] = useState();
  const [selectedFacility, setSelectedFacility] = useState(null);
  const [mode, setMode] = useState(PageMode.Add);
  const handleClose = () => setShow(false);
  const handleShow = () => {
    setShow(true);
    setShowSuccess(false);
  };
  const handleCloseDelete = () => setShowDelete(false);
  const handleShowDelete = () => {
    setShowDelete(true);
    setShowSuccess(false);
  };

  useEffect(async () => {
    setLoading(true);
    const response = await FacilityService.getAllFacilities();
    setMessage(!!response?.message && response?.message);
    if (response.success) {
      setFacilitiesData(
        response.body?.map((s) => {
          return {
            ...s,
            cityName: s.city?.name,
            cityId: s.city?.id,
            countryName: s.city?.country?.name,
            countryId: s.city?.country?.id,
          };
        })
      );
    }

    setLoading(false);
  }, []);

  const deleteFacilityHandler = async (id) => {
    setDeleting(true);

    const response = await FacilityService.deleteFacility(id);

    setDeleting(false);
    if (!!!response.success && !response.success) {
      setMessageDelete(!!response.message && response.message);
      return;
    }
    handleCloseDelete();
    setFacilitiesData(facilitiesData.filter((u) => u.id != id));

    if (!!!response.success && !response.success) {
      setMessage(!!response?.message && response?.message);
      // To-Do: implemet error msg
      setLoading(false);
      return;
    }
    setLoading(false);
  };
  const updateFacilityStatusHandler = async (id, active) => {
    setLoading(true);
    let requestData = new FormData();
    const facilityData = {
      active: !active,
    };
    requestData.append("request", JSON.stringify(facilityData, 0, 2));
    const response = await FacilityService.updateFacility(id, requestData);

    if (!!!response.success && !response.success) {
      // To-Do: implemet error msg
      setMessage(!!response?.message && response?.message);
      setLoading(false);
      return;
    }

    setFacilitiesData(
      facilitiesData?.map((s) => {
        return s.id === response?.body?.id
          ? { ...s, active: response?.body?.active }
          : s;
      })
    );
    setLoading(false);
  };
  const columns = [
    { id: "name", label: t("Facility.Name") },
    { id: "countryName", label: t("Facility.Country") },
    { id: "cityName", label: t("Facility.City") },
    { id: "active", label: t("Facility.Active"), type: TableInputType.Toggle },
  ];
  let tableContent = (
    <DataTable
      columns={columns}
      rows={facilitiesData}
      showActions={true}
      onEdit={(facility) => {
        handleShow();
        setSelectedFacility(facility);
        setMode(PageMode.Update);
      }}
      onDelete={(facility) => {
        handleShowDelete();
        setSelectedFacility(facility);
        setMessageDelete(null);
      }}
      onUpdateStatus={(facility) => {
        updateFacilityStatusHandler(facility.id, facility.active);
      }}
    />
  );

  return (
    <div>
      <div className="content">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-5 col-lg-4 col-xl-3 theiaStickySidebar">
              <StickyBox offsetTop={20} offsetBottom={20}>
                <DashboardSidebar />
              </StickyBox>
            </div>
            <div className="col-md-7 col-lg-8 col-xl-9">
              <div className="row">
                <div className="col-sm-12">
                  <div className="card">
                    <div className="card-header">
                      <h4 className="card-title float-left">
                        {t("Facility.ListTitle")}
                      </h4>
                      <button
                        className="btn btn-primary float-right"
                        onClick={() => {
                          handleShow();
                          setSelectedFacility(null);
                          setMode(PageMode.Add);
                        }}
                        data-toggle="modal"
                      >
                        {t("Facility.Add")}
                      </button>
                    </div>
                    <div className="card-body ">
                      <div className="card card-table mb-0">
                        <div className="card-body">
                          <div className="table-responsive">
                            <div className="invalid-feedback d-block mb-2">
                              {message}
                            </div>
                            {loading ? (
                              <div className="centered">
                                <h4>
                                  <div>
                                    <CircularProgress />
                                  </div>
                                  <div>
                                    <span style={{ color: "#15558d" }}>
                                      {t("Facility.GettingFacilities")}
                                    </span>
                                  </div>
                                </h4>
                              </div>
                            ) : (
                              <>
                                {showSuccess ? (
                                  <div style={{ margin: "20px" }}>
                                    <Alert severity="success">
                                      {mode === PageMode.Add
                                        ? t(
                                            "Facility.AddingSuccessfullyMessage"
                                          )
                                        : t(
                                            "Facility.UpdatingSuccessfullyMessage"
                                          )}
                                    </Alert>
                                  </div>
                                ) : (
                                  <></>
                                )}
                                {tableContent}
                              </>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        show={show}
        onHide={handleClose}
        size="lg"
        scrollable={true}
        aria-labelledby="example-modal-sizes-title-sm"
      >
        <Modal.Header>
          <h5 className="modal-title">{`${t(mode)} ${t(
            "Facility.Facility"
          )}`}</h5>
          <button
            type="button"
            className="close"
            data-dismiss="modal"
            aria-label="Close"
            onClick={handleClose}
          >
            <span aria-hidden="true">×</span>
          </button>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-body">
            <div>
              <AddUpdateForm
                selectedFacility={selectedFacility}
                mode={mode}
                onClose={(facility) => {
                  handleClose();
                  if (facility) {
                    if (mode === PageMode.Update) {
                      const updatedFacility = facilitiesData.map((u) => {
                        return u.id === facility.id
                          ? {
                              ...facility,
                              cityName: facility.city.name,
                              cityId: facility.city.id,
                              countryName: facility.city?.country?.name,
                              countryId: facility.city?.country?.id,
                            }
                          : u;
                      });
                      setFacilitiesData(updatedFacility);
                    } else {
                      const addedFacility = {
                        ...facility,
                        cityName: facility.city.name,
                        cityId: facility.city.id,
                        countryName: facility.city?.country?.name,
                        countryId: facility.city?.country?.id,
                      };
                      setFacilitiesData([...facilitiesData, addedFacility]);
                    }
                    setShowSuccess(true);
                  }
                }}
              />
            </div>
          </div>
        </Modal.Body>
      </Modal>

      <Modal
        show={showDelete}
        onHide={handleCloseDelete}
        aria-labelledby="example-modal-sizes-title-sm"
        centered
      >
        <Modal.Header>
          <h5 className="modal-title">
            {t("Facility.DeleteConfirmationTitle")}
          </h5>
          <button
            type="button"
            className="close"
            data-dismiss="modal"
            aria-label="Close"
            onClick={handleCloseDelete}
          >
            <span aria-hidden="true">×</span>
          </button>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-body">
            {messageDelete && (
              <div className="invalid-feedback d-block mb-2">
                {messageDelete}
              </div>
            )}
            <h5 style={{ color: "red" }}>
              {t("Facility.DeleteConfirmationMessage")}
            </h5>
            <div>
              <b>{t("Facility.Facility")}:</b>
              {` ${selectedFacility?.name} `}
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn-danger submit-btn"
            disabled={deleting}
            onClick={() => {
              deleteFacilityHandler(selectedFacility.id);
            }}
          >
            {!deleting ? t(`Facility.Delete`) : ""}
            {deleting && (
              <span className="spinner-border spinner-border-sm mb-1"></span>
            )}
          </button>
          <button
            className="btn btn-secondary submit-btn"
            onClick={handleCloseDelete}
          >
            {t("Facility.Close")}
          </button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}
export default FacilitiesList;
